import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
	BarChart,
	Bar,
	XAxis,
	YAxis,
	CartesianGrid,
	Tooltip,
	Legend,
	ResponsiveContainer,
} from 'recharts';

const useStyles = makeStyles({
	chartContainer: {
		width: '90%',
		height: 250,
		paddingBottom: 30,
	},
	chartTile: {
		color: '#fff',
	},
	barChart: {
		margin: 'auto',
	},
});

const BarGraph = ({ data, title }) => {
	const classes = useStyles();

	if (data == null) {
		data = [
			{
				name: 'Page A',
				uv: 4000,
				pv: 2400,
				amt: 2400,
			},
			{
				name: 'Page B',
				uv: 3000,
				pv: 1398,
				amt: 2210,
			},
			{
				name: 'Page C',
				uv: 2000,
				pv: 9800,
				amt: 2290,
			},
			{
				name: 'Page D',
				uv: 2780,
				pv: 3908,
				amt: 2000,
			},
			{
				name: 'Page E',
				uv: 1890,
				pv: 4800,
				amt: 2181,
			},
			{
				name: 'Page F',
				uv: 2390,
				pv: 3800,
				amt: 2500,
			},
			{
				name: 'Page G',
				uv: 3490,
				pv: 4300,
				amt: 2100,
			},
		];
	}

	return (
		<div className={classes.chartContainer}>
			<h3 className={classes.chartTile}>{title}</h3>
			<ResponsiveContainer>
				<BarChart data={data}>
					<CartesianGrid strokeDasharray='3 3' />
					<XAxis dataKey='name' tick={{ fill: '#fff' }} />
					<YAxis tick={{ fill: '#fff' }} />
					<Tooltip />
					<Legend />
					<Bar dataKey='pv' fill='#8884d8' />
					<Bar dataKey='uv' fill='#82ca9d' />
				</BarChart>
			</ResponsiveContainer>
		</div>
	);
};

export default BarGraph;
